.ReportPage {
    width: 100%;
    height: 100%;
    min-height: calc(100vh - 110px) !important;
}

.reportButton {
    margin: auto;
    padding: 15px 35px;
    width: 100%;
    font-size: 30px;
}

.reportButton:hover {
    opacity: 0.9;
}

.reportButton:active {
    opacity: 0.8;
}

.emailButton {
    color: white;
    background: gray;
}

.githubButton {
    color: white;
    background: black;
}